<template>
  <v-container fluid class="pt0">
    
    <!-- Page title and search -->
    <v-card class="elevation-0 mb0 row d-flex flex-column ml1 mr1"
      style="background: transparent; border: none">
      <v-container fluid class="pl1 pr1 pt2">
        <v-layout row>
          <v-flex xs5 class="d-flex align-items-end">
             <h3 class="csp-light-blue-text font-size-20">Pending Applications</h3>
          </v-flex>

          <v-flex xs7 d-flex class="">
            <v-container fluid class="p0">
              <v-layout row class="">
                <v-flex xs6>

                </v-flex>
                <v-flex d-flex xs6 class="">
                  <v-text-field
                    append-icon="search"
                    label="SEARCH"
                    hide-details
                    class="xs4 font-size-11"
                    v-model="search">
                  </v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>

    <!-- Table -->
    <v-card class="elevation-0 mb10">
      <div class="heightt-300px " style="overflow-y: scroll"> <!-- Used so the market data table scrolls -->
        <v-data-table
        hide-actionss
        select-all="blue"
        v-model="selected"
        selected-key="id"
        :items="pendingApplications"
        :search="search"
        :loading="loadingDataTable"
        :rows-per-page-items="rowsPerPageItems"
        :no-data-text="'No applications meet this search criteria'"
        class="elevation-1 marketDataTable table-striped table-bordered">
        <template slot="headers" scope="props">
          <tr class="bg-csp-light-blue marketDataTableHeader">
            <th class="text-left">APPLICATION DATE</th>
           <th class="text-left">APPLICANT NAME</th>
            <th class="text-left">STATUS</th>
            <th class="text-center width-120px white--text ">ACTIONS</th>
          </tr>
        </template>

        <template slot="items" scope="props">
          <tr :active="props.selected" @click="props.selected = !props.selected">
              <td>{{ props.item.application_date | moment('DD-MMM-YYYY')}}</td>
              <td>{{ props.item.formattedApplicantName}}</td>
              <td>{{ props.item.ipo_request.process_status }}</td>
              <td class="font-size-10 text-center width-120px d-flex align-center justify-center" style="padding: 0px; border: 0px">
                <!-- APPROVE -->
                <v-btn
                  data-toggle="tooltip" data-placement="top" title="ACCEPT" icon class="m0 p0 width-30px height-30px"
                  @click="showApplicationDialog('APPROVE',props.item, $event)">
                  <v-icon class="green--text font-weight-bold font-size-18" >
                    check_circle
                  </v-icon>
                </v-btn>
                <!--EDIT -->
               <v-btn
                data-toggle="tooltip" data-placement="top" title="EDIT" icon class="m0 p0 width-30px height-30px"
                @click="showApplicationDialog('EDIT',props.item, $event)">
                <v-icon  class="blue--text font-weight-bold font-size-18" >
                  mode_edit
                </v-icon>
               </v-btn>
               <!-- VIEW -->
                <v-btn
                  data-toggle="tooltip" data-placement="top" title="VIEW"
                  icon class="m0 p0 width-30px height-30px"
                  @click="showApplicationDialog('VIEW',props.item, $event)">
                  <v-icon class="color-csp-blue ml4 fa fa-eye fa-2x btn-icon font-weight-bold font-size-18">
                  </v-icon>
                </v-btn>
                <!-- REJECT -->
                <v-btn
                  data-toggle="tooltip" data-placement="top" title="REJECT" icon class="m0 p0 width-30px height-30px"
                  @click="showApplicationDialog('REJECT',props.item, $event)">
                  <v-icon class="red--text font-weight-bold font-size-18" >
                    close
                  </v-icon>
                </v-btn>
              </td>
          </tr>
        </template>
      </v-data-table>
      </div>
    </v-card>

    <!-- Dialog shown before rejecting an application -->
    <reject-application-dialog 
      :application="application" 
      :show="showRejectApplicationDialog">
    </reject-application-dialog>

    <!-- Dialog shown when approving an application -->
    <approve-application-dialog
      :application="application"
      :show="showApproveApplicationDialog">
    </approve-application-dialog>

    <!-- Dialog shown when editing an application -->
    <edit-application-dialog
      :application="application"
      :show="showEditApplicationDialog">
    </edit-application-dialog>

    <!-- Dialog shown when viewing an application -->
    <view-application-dialog
      :application="application"
      :show="showViewApplicationDialog">
    </view-application-dialog>

  </v-container>
</template>


<script>
import RejectApplicationDialog from "./dialogs/RejectApplicationDialog";
import ApproveApplicationDialog from "./dialogs/ApproveApplicationDialog";
import EditApplicationDialog from "./dialogs/EditApplicationDialog";
import ViewApplicationDialog from "./dialogs/ViewApplicationDialog";

import { mapGetters } from "vuex";

export default {
  components: {
    RejectApplicationDialog,
    ApproveApplicationDialog,
    EditApplicationDialog,
    ViewApplicationDialog
  },

  data() {
    return {
      application: {},
      search: "",
      selected: [],
      loadingDataTable: false,
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }],
      showRejectApplicationDialog: false,
      showApproveApplicationDialog: false,
      showEditApplicationDialog: false,
      showViewApplicationDialog: false
    };
  },

  computed: {
    ...mapGetters({
      pendingApplications: "pendingApplications"
    })
  },

  methods: {
    /**
     * Used to toggle the select all functionality
     */
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else this.selected = this.applications.slice();
    },

    /**
     * Show the confirmation dialog box before rejecting an application
     */
    showApplicationDialog(type, application, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.application = application;

      // Display the edit popup modal/dialog
      if (type === "REJECT") {
        document.querySelector("#openRejectApplicationDialog").click();
      } else if (type === "APPROVE") {
        document.querySelector("#openApproveApplicationDialog").click();
      } else if (type === "EDIT") {
        document.querySelector("#openEditApplicationDialog").click();
      } else if (type === "VIEW") {
        document.querySelector("#openViewApplicationDialog").click();
      }

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    }
  }
};
</script>


<style scoped lang="sass">
  .marketDataTable
    margin-bottom: 0px !important

  .marketDataTableHeader,
  .marketDataTableBody
    height: 25px

  .marketDataTableHeader
    th
      padding: 5px 5px !important
      color: #FFFFFF !important
      font-weight: bold
      font-size: 10px

  .marketDataTableBody
    border-bottom: none !important
    &:hover
      background: #CCC !important
      cursor: pointer

  .marketDataTableBody
    td
      padding: 5px 5px !important
      height: 25px
      color: #31708f
    td:last-child
      border-bottom: 0px solid transparent !important
  .btn__content
    padding: 0px !important
  .eemulate-link
    &:hover
      transition: all .2s
      text-decorations: underline
      border-bottom: 2px solid #1a2155
      padding-bottom: 2px
      cursor: pointer
      background: #EEEEEE

  .selectedRow
    background: #b0bed9 !important
    font-weight: bold
</style>



