import Vue from "vue";
import Router from "vue-router";
import Login from "@/components/Login";
// import Dashboard from "@/components/Dashboard";
import PageNotFound from "@/components/404";

/**
 * E-IPO ROUTES
 */
import Dashboard from "../components/eipo_components/agentModule/dashboard/Dashboard";
import AgentAddUser from "../components/eipo_components/agentModule/agent/AddUser";
import AgentListUsers from "../components/eipo_components/agentModule/agent/ListUsers";
import InitiateApplication from "../components/eipo_components/agentModule/applications/InitiateNewApplication";
import PendingApplications from "../components/eipo_components/agentModule/applications/PendingApplications";
import AcceptedApplications from "../components/eipo_components/agentModule/applications/AcceptedApplications";
import RejectedApplications from "../components/eipo_components/agentModule/applications/RejectedApplications";
import SubmittedApplications from "../components/eipo_components/agentModule/applications/SubmittedApplications";
import CreateBatch from "../components/eipo_components/agentModule/batches/CreateBatch";
import ListBatches from "../components/eipo_components/agentModule/batches/ListBatches";
import Reports from "../components/eipo_components/agentModule/reports/Reports";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/ipo/agent/addUser",
      name: "agent-add-user",
      component: AgentAddUser
    },
    {
      path: "/ipo/agent/listUsers",
      name: "agent-list-users",
      component: AgentListUsers
    },
    {
      path: "/initiate-application",
      name: "initiate-application",
      component: InitiateApplication
    },
    {
      path: "/ipo/applications/pending",
      name: "pending-applications",
      component: PendingApplications
    },
    {
      path: "/ipo/applications/accepted",
      name: "accepted-applications",
      component: AcceptedApplications
    },
    {
      path: "/ipo/applications/rejected",
      name: "rejected-applications",
      component: RejectedApplications
    },
    {
      path: "/ipo/applications/submitted",
      name: "submitted-applications",
      component: SubmittedApplications
    },
    {
      path: "/ipo/batch/create",
      name: "create-batch",
      component: CreateBatch
    },
    {
      path: "/ipo/batches/list",
      name: "list-batches",
      component: ListBatches
    },
    {
      path: "/ipo/reports",
      name: "reports",
      component: Reports
    },
    {
      path: "/dashboard",
      name: "Dashboard",
      component: Dashboard
    },

    // END OF E-IPO ROUTES
    {
      path: "/public",
      redirect: ""
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "*",
      name: "PageNotFound",
      component: PageNotFound
    }
  ]
});



