export interface IApplicantDetails {
  nok_name: string;
  nok_relationship: string;
  nok_phone: string;
}

export enum EApplicationTypeId {
  "Individual" = 1,
  "Joint" = 2,
  "Corporate" = 3
}

/**
 * Interface for an applicant's name
 *
 * @export
 * @interface IApplicant
 */
export interface IApplicant {
  firstname: string;
  lastname: string;
  other_names: string;
  phone: string;
}

/**
 * Interface defining the structure for a bid made for an IPO
 *
 * @export
 * @interface IBid
 */
export interface IBid {
  bid_price: number;
  bid_share: number;
  bid_amount: number;
}

export interface IAddress {
  address: string;
  city: string;
  state: string;
}

export interface IBankDetail {
  bank_name: string;
  account_name: string;
  account_number: string;
  sort_code: string;
  bvn: string;
  city: string;
  state: string;
}

export interface IpoRequest {
  stock_broker_id: number;
  cscs_account: string;
  chn: string;
  status: boolean;
  process_status: string;
  bids: Array<IBid>;
}

export interface ICorporate {
  companyName: string;
  cacNumber: string;
}

/**
 * Interface defining the structure of an IPO application
 *
 * @export
 * @interface IApplication
 */
export interface IApplication {
  id: number;
  email: string;
  ipo_id: number;
  token: string;
  batched: boolean;
  application_type_id: number;
  stock_broker_id: number;
  applicants: Array<IApplicant>;
  applicant_details: Array<IApplicantDetails>;
  address: IAddress;
  bank_detail: IBankDetail;
  corporate: ICorporate;
  ipo_request: IpoRequest;
  formattedApplicantName?: string;
}



