import * as mutationTypes from "../../../mutation-types";
import { state } from "./agent";
import { TransformAPiApplicationData } from "./agent.transformers";
import {
  ICompany,
  IBatch,
  IAgentAuthenticateResponse
} from "../../../../models";

const mutations = {
  /**
   * Save the agents, ipo applications, and batches that belong the company which the authenticated
   * agent belongs to.
   *
   * @param {any} state
   * @param {any} companyData
   */
  [mutationTypes.SAVE_COMPANY_AGENTS_APPLICATIONS_AND_BATCHES_TO_STORE](
    state,
    userData: IAgentAuthenticateResponse
  ) {
    let agents;
    let applications;
    let batches;
    let company;
    let user;

    // Ensure that the data exists, if not default to []
    try {
      console.log(userData);
      user = userData.user;
      company = userData.company;
      agents = company.agents;
      applications = company.applications;
      batches = company.batches;

      applications =
        applications.constructor === Array ? applications : [applications];

      agents = agents.constructor === Array ? agents : [agents];
      batches = batches.constructor === Array ? batches : [batches];

      if (!agents) agents = [];
      if (!applications) applications = [];
      if (!batches) batches = [];
      if (!company) company = {};
      if (!user) user = {};
    } catch (e) {
      console.log(e);
    }

    // Save the various pieces of data to the store.
    state.agents = agents;
    state.applications = applications;
    state.batches = batches;
    state.company = company;
    state.user = user;
  },

  // /**
  //  * Save the agent's profile data upon authentication
  //  */
  // [mutationTypes.SAVE_AGENT_DATA](state, agent_data) {
  //   state.agent_data = agent_data;
  // },

  // [mutationTypes.SAVE_AGENT_USERS_DATA](state, user_data) {
  //   state.agent_data.agents = user_data;
  // },

  // // Store the company, user and role data about the authenticated agent
  // [mutationTypes.SAVE_COMPANY_AGENT_DATA_TO_STORE](state, data) {
  //   state.agent_data.company = data.company;
  //   state.agent_data.user = data.user;
  //   state.agent_data.role = data.role;
  // },

  /**
   * Save the applications, and agents of the stockbroker company in the store
   */
  [mutationTypes.SAVE_APPLICATIONS_AND_AGENTS_TO_STORE](
    state,
    companyData: ICompany
  ) {
    state.agents = companyData.agents;
    state.applications = companyData.applications;
  },

  /**
   * Save the batches owned by the stockbroker company into the store
   */
  [mutationTypes.SAVE_BATCHES_IN_STORE](state, batches: Array<IBatch>) {
    state.batches = batches;
  },

  /**
   * Clear the entire store (necessary for logging out of the application)
   */
  [mutationTypes.CLEAR_AGENT_MODULE_STORE](currentState) {
    const initialState = state();
    Object.keys(initialState).forEach(key => {
      currentState[key] = initialState[key];
    });
  }
};

export default mutations;



