/**
 * This service file handles calling of services, and making store mutations with their responses.
 * I honestly don't know where best to put these kinda actions, so this is a stop gap solution begging
 * for refactoring from a smarter mind, or future me :)
 *
 */

// import * as ApplicationService from "./ApplicationService";
import * as UserService from "./UserService";
import * as BatchService from "./BatchServices";
import * as mutationTypes from "../store/mutation-types";
import store from "../store";

/**
 * Get and save, applications and agents belonging to the stockbroker company to the store
 *
 * @param stockbroker_id
 */
export const SaveStockbrokerInfoToStore = stockbroker_id => {
  UserService.GetStockbrokerInfo(stockbroker_id)
    .then(response => {
      const stockbrokerCompanyData = response.data.data;
      store.commit(
        mutationTypes.SAVE_APPLICATIONS_AND_AGENTS_TO_STORE,
        stockbrokerCompanyData
      );
    })
    .catch(error => {
      console.log(error);
    });
};

/**
 * Get and save the batches owned by the stockbroker company to the store
 *
 * @param stockbroker_id
 */
export const SaveBatchesOwnedByStockbrokersToStore = stockbroker_id => {
  BatchService.GetBatches(stockbroker_id)
    .then(response => {
      // Dispatch the action to store the batches to our vuex store
      store.commit(mutationTypes.SAVE_BATCHES_IN_STORE, response.data.data);
    })
    .catch(err => {
      console.log(err);
    });
};



