<!-- ADD USER -->
<template>
  <v-container class="pt0 mt10" fluid>
    <v-layout row>

      <!-- Add user form -->
      <v-flex
        sm12 lg12
        class="mb10">
        
        <!-- Card Housing entire add user -->
        <v-card>

          <v-card-title primary-title class="font-weight-bold p2 d-flex justify-start">
            <h5 class=" pt10 font-weight-bold pl5 mb-0 font-size-11 text-uppercase">
              <span class="font-size-17 font-weight-normal">ADD A NEW USER</span>
            </h5>
          </v-card-title>

          <v-card-media>
            <v-container fluid class="pt0 pl1 pr1">
              <v-layout row>
                <v-form v-model="valid" ref="addUserForm" class="w100p">

                  <v-layout row class="pl20 pr20 pt10">

                    <!-- First Name -->
                    <v-flex xs6 class="height-55px mb6">
                      <v-text-field  :label="'FIRST NAME'" v-model="firstName" :type="'text'" :rules="requiredRule"  
                      v-validate="'required'" name="firstName" :disabled="inPreviewState">
                      </v-text-field>
                    </v-flex>

                    <!-- Other Name -->
                    <v-flex xs6 class="height-55px mb6">
                      <v-text-field  :label="'OTHER NAME'" v-model="otherName" :type="'text'" :rules="requiredRule"  
                      v-validate="'required'" name="otherName" :disabled="inPreviewState">
                      </v-text-field>
                    </v-flex>

                    <!-- Last Name -->
                    <v-flex xs6 class="height-55px mb6">
                      <v-text-field  :label="'LAST NAME'" v-model="lastName" :type="'text'" :rules="requiredRule"  
                      v-validate="'required'" name="lastName" :disabled="inPreviewState">
                      </v-text-field>
                    </v-flex>

                    <!-- Gender -->
                    <v-flex xs6 class="height-55px mb6">
                      <v-select class="" :label="'GENDER'" :items="genderTypes" v-model="gender" v-validate="'required'" name="gender" :rules="requiredRule"
                      :disabled="inPreviewState">
                      </v-select>
                    </v-flex>

                    <!-- Phone Number -->
                    <v-flex xs6 class="height-55px mb6">
                      <v-text-field :label="'PHONE NUMBER'" v-model="phoneNumber" :type="'number'" 
                      :rules="requiredRule"  v-validate="'required'" name="phoneNumber" :disabled="inPreviewState">
                      </v-text-field>
                    </v-flex>

                    <!-- Email -->
                    <v-flex xs6 class="height-55px mb6">
                      <v-text-field :label="'EMAIL'" v-model="email" :type="'text'" 
                      :rules="emailRules"  v-validate="'required|email'" name="email" :disabled="inPreviewState">
                      </v-text-field>
                    </v-flex>

                  </v-layout>

                   <!-- Loading Icon when adding a user -->
                  <loading v-if="isAddingUser" :loadingText="'Adding New User'" class="mt20"></loading>

                  <v-layout  row class="mt20">
                    <v-flex xs6 class="d-flex justify-end ">
                      <v-btn style="background: #4c7396; color: #FFFFFF"
                        @click="addUser()">
                        Add User
                      </v-btn>
                    </v-flex>

                    <v-flex xs6>
                      <v-btn v-if="!inPreviewState" class="red darken-1 white--text" @click="cancel()">
                        Cancel
                      </v-btn>
                    </v-flex>
                  </v-layout>

                </v-form>


              </v-layout>
            </v-container>
          </v-card-media>

        </v-card>

      </v-flex>

      <!-- Snackbar to show success message when user is added -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="showUserAddedSnackbar"
        >
        New User has been successfully created.
      </v-snackbar>

      <!-- Snackbar to show error message when user addition fails -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="showUserAddedErrorSnackbar"
        >
        An error occured while creating this user. Please try again later.
      </v-snackbar>

    </v-layout>

  </v-container>

</template>


<script>
import { AddNewUser } from "../../../../services/UserService";
import ConfigData from "../../../../services/ConfigDataService";
import { mapGetters } from "vuex";
import { SaveStockbrokerInfoToStore } from "../../../../services/MutationServices";

import Loading from "../../../shared/Loading";

export default {
  components: {
    Loading
  },

  data() {
    return {
      snackbarTimeout: 4000,
      snackbarMode: "",
      showUserAddedSnackbar: false,
      showUserAddedErrorSnackbar: false,
      isAddingUser: false,
      valid: false,
      inPreviewState: false,
      firstName: null,
      otherName: null,
      lastName: null,
      gender: null,
      phoneNumber: null,
      email: null,
      genderTypes: [
        { text: "Male", value: "M" },
        { text: "Female", value: "F" }
      ],
      requiredRule: [v => !!v || "Required"],
      /*eslint-disable */
      emailRules: [
        v => !!v || "E-mail is required",
        v =>
          /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
          "E-mail must be valid"
      ]
      /*eslint-enable */
    };
  },

  computed: {
    ...mapGetters({
      companyID: "getCompanyID"
    })
  },

  methods: {
    addUser: function() {
      // Validate the addUser form
      this.$validator.validateAll().then(result => {
        // Validation errors occured
        if (!result) {
          // Used to display the errors to the user, if the preview btn is pressed
          let formComponents = this.$refs.addUserForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });

          return;
        }

        // Form is valid, so continue processing the data
        const newUser = {
          first_name: this.firstName,
          lastName: this.lastName,
          other_names: this.otherName,
          email: this.email,
          gender: this.gender,
          phone: this.phoneNumber,
          role_id: ConfigData.agentRoleID,
          company_id: this.companyID,
          username: "default_username",
          password: ConfigData.defaultAgentPassword()
        };

        this.inPreviewState = true;
        this.isAddingUser = true;

        AddNewUser(newUser)
          .then(response => {
            this.showUserAddedSnackbar = true;
            this.inPreviewState = false;
            this.isAddingUser = false;

            // Refresh the list of users
            SaveStockbrokerInfoToStore(this.companyID);

            // Clear the form data
            this.$refs.addUserForm.reset();
          })
          .catch(error => {
            console.log(error.message);
            this.showUserAddedErrorSnackbar = true;
            this.inPreviewState = false;
            this.isAddingUser = false;
          });
      });
    }
  }
};
</script>

<style lang="sass" scoped>

</style>




