<template>
  <div>
    <v-dialog v-model="showRejectApplicationDialog" lazy absolute persistent width="40%">
        <!--Button used to trigger the opening of the edit user dialog -->
        <v-btn id="openRejectApplicationDialog"
        style="display: none" color="primary" dark slot="activator" ref="triggerButton">
        </v-btn>

        <v-card>
          <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
              <h5 class="white--text font-size-20px">REJECT APPLICATION</h5>
          </v-card-title>
            <!-- Content -->
          <v-card-text>
              <v-layout>
              <v-flex>
                  
                  You are about to reject the following application. 
                  <!-- Kindly note that rejecting an application is an irreversible action.<br>
              <b class="mt10">Do you agree to reject the application?</b> -->

              <table class="table mt20">
                  <tr>
                  <th>Applicant Name</th>
                  <td>{{application.formattedApplicantName}}</td>
                  </tr>
                  <tr>
                  <th>Application Date</th>
                  <td>{{application.application_date | moment('DD-MMM-YYYY')}}</td>
                  </tr>
                  <!-- <tr>
                  <th>No Of Units</th>
                  <td>{{application.units}}</td>
                  </tr> -->
              </table>
              </v-flex>
              </v-layout>

              <v-form v-model="valid" ref="rejectApplicationForm" class="w100p pl5">
              <v-text-field
                  v-model="reasonForRejectingApplication"
                  label="Reason for rejecting this application"
                  v-validate="'required'"
                  :rules="requiredRule"
                  multi-line
              ></v-text-field>
              </v-form>

                <!-- Loading Icon when approving application-->
                <v-layout v-if="isRejectingApplication"  row class=" d-flex justify-center ">
                  <v-flex class="xs6 d-flex justify-center align-center">
                    <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                    <span class="font-size-12 ml5">Rejecting Application...</span>
                  </v-flex>
                </v-layout>

              <!-- Buttons -->
              <v-container fluid class="p0 ">
                  <v-layout  row class="mt20">
                      <v-flex xs12 lg6>
                      <v-btn small block info class=" darken-1 white--text"
                      @click="rejectApplication(application)"
                      :disabled="isRejectingApplication" :loading="isRejectingApplication">
                      REJECT THIS APPLICATION
                      </v-btn>
                  </v-flex>
                  <v-flex xs12 lg6>
                      <v-btn small block class="red darken-1 white--text"
                      @click="cancelRejectApplication()">
                      DO NOT REJECT THIS APPLICATION
                      </v-btn>
                  </v-flex>
                  </v-layout>
              </v-container>
          </v-card-text>
        </v-card>
    </v-dialog>

      <!-- Snackbar to show success message when application is rejected -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="applicationRejected"
        >
        Application has been successfully rejected
      </v-snackbar>

      <!-- Snackbar to show error message when application rejection fails -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="applicationRejectionFailed"
        >
        Failed to reject the application
      </v-snackbar>
  </div>
</template>


<script>
import { mapGetters } from "vuex";
import { RejectApplication } from "../../../../../services/ApplicationService";
import { SaveStockbrokerInfoToStore } from "../../../../../services/MutationServices";

export default {
  props: {
    application: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      isRejectingApplication: false,
      applicationRejected: false,
      applicationRejectionFailed: false,
      showRejectApplicationDialog: false,
      valid: false,
      reasonForRejectingApplication: "",
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    ...mapGetters({
      userID: "getAgentID",
      companyID: "getCompanyID"
    })
  },

  methods: {
    /**
     * Show the confirmation dialog box before rejecting an application
     */
    showRejectApplication(application, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.application = application;

      // Display the edit popup modal/dialog
      this.$refs.triggerButton.click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    /**
     * Used to reject an application
     */
    rejectApplication(application) {
      this.$validator.validateAll().then(result => {
        // The form is invalid
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.rejectApplicationForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          // Send the rejection request to the server

          this.isRejectingApplication = true;

          const rejectionRequest = {
            user_id: this.userID,
            application_id: application.id,
            comment: this.reasonForRejectingApplication,
            token: application.token
          };

          RejectApplication(rejectionRequest)
            .then(response => {
              this.isRejectingApplication = false;
              this.applicationRejected = true;

              // Refresh the applications
              SaveStockbrokerInfoToStore(this.companyID);

              this.cancelRejectApplication();
            })
            .catch(err => {
              this.isRejectingApplication = false;
              this.applicationRejectionFailed = true;

              console.log(err);
            });
        }
      });
    },

    cancelRejectApplication: function() {
      // Close the dialog modal
      this.showRejectApplicationDialog = false;

      // Clear the selected user
      //   this.application = {};

      // Clear the rejection message
      this.reasonForRejectingApplication = "";
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



