import axios from "axios";
import * as ApiUrls from "./ApiUrlService";

/**
 * Create a new batch
 * @param data
 */
export const CreateBatch = data => {
  return axios({
    method: "post",
    url: ApiUrls.BatchResourceUrl,
    data
  });
};

/**
 * Get a list of all batches owned by a broker
 * @param data
 */
export const GetBatches = data => {
  return axios({
    method: "get",
    url: ApiUrls.BatchResourceUrl,
    data
  });
};

/**
 * Submit a batch
 * @param data
 */
export const SubmitBatches = data => {
  return axios({
    method: "post",
    url: ApiUrls.SubmitBatchUrl,
    data
  });
};

/**
 * Delete Batch
 * @param data
 */
export const DeleteBatches = data => {
  return axios({
    method: "delete",
    url: ApiUrls.DeleteBatchUrl + "/" + data.batch_id,
    data
  });
};

/**
 * Add applications to a batch
 * @param data
 */
export const AddApplicationsToBatch = data => {
  return axios({
    method: "post",
    url: ApiUrls.BatchContentResourceUrl,
    data
  });
};



