<template>
    <div>
        <v-dialog v-model="showAddApplicationsToBatchDialog" lazy absolute persistent width="40%">
            <!--Button used to trigger the opening of the edit user dialog -->
            <v-btn id="openAddApplicationsToBatchDialog"
            style="display: none" color="primary" dark slot="activator" ref="triggerButton">
            </v-btn>

            <v-card>
            <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px">ADD APPLICATION(S) TO A BATCH</h5>
            </v-card-title>
            <!-- Content -->
            <v-card-text>
                <v-layout>
                    <v-flex>
                        <strong>You are about to add application(s) to a batch.</strong>

                        <v-form v-model="valid" ref="addApplicationsForm" class="w100p pl5">
                             <v-select class="" :label="'SELECT BATCH'" :items="companyBatches" 
                                v-model="selectedBatch" v-validate="'required'" name="batch" :rules="requiredRule">
                            </v-select>
                        </v-form> 
                    </v-flex>
                </v-layout>

                <!-- Loading Icon when approving application-->
                <v-layout v-if="isAddingApplications"  row class=" d-flex justify-center ">
                    <v-flex class="xs6 d-flex justify-center align-center">
                        <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                        <span class="font-size-12 ml5">Adding Application(s) to batch...</span>
                    </v-flex>
                </v-layout>

                <!-- Buttons -->
                <v-container fluid class="p0 ">
                    <v-layout  row class="mt20">
                        <v-flex xs12 lg6>
                            <v-btn small block info class=" darken-1 white--text"
                            @click="addApplicationsToBatch()"
                            :disabled="isAddingApplications" :loading="isAddingApplications">
                            ADD APPLICATIONS
                            </v-btn>
                        </v-flex>
                        <v-flex xs12 lg6>
                            <v-btn small block class="red darken-1 white--text"
                            @click="cancelAddApplicationsDialog()">
                            CANCEL
                            </v-btn>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            </v-card>
        </v-dialog>

      <!-- Snackbar to show success message when applications are added to batches -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="addedToBatch"
        >
        The application(s) have been successfully added to the batch
      </v-snackbar>

      <!-- Snackbar to show error message when adding applications to a batch fails -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="addingToBatchFailed"
        >
        {{ errorMessage }}
      </v-snackbar>
    </div>
</template> 


<script>
import { mapGetters } from "vuex";
import { AddApplicationsToBatch } from "../../../../../services/BatchServices";
import {
  SaveStockbrokerInfoToStore,
  SaveBatchesOwnedByStockbrokersToStore
} from "../../../../../services/MutationServices";

export default {
  props: {
    applications: {
      type: Array,
      default: []
    },
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      addedToBatch: false,
      addingToBatchFailed: false,
      errorMessage:
        "Error occured while adding the application(s) to the batch",
      showAddApplicationsToBatchDialog: false,
      isAddingApplications: false,
      valid: false,
      selectedBatch: null,
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    ...mapGetters({
      batches: "batches",
      companyID: "getCompanyID"
    }),

    /**
     * Used to add (text and value properties) to each batch, so it plays nicely with vue-select
     */
    companyBatches: function() {
      const companyBatches = this.batches.map(batch => {
        batch.text = batch.batch_name;
        batch.value = batch.id;

        return batch;
      });

      return companyBatches;
    }
  },

  methods: {
    addApplicationsToBatch() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.addApplicationsForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          const applicationsToBeBatched = this.applications.map(application => {
            let request = {
              application_id: application.id
            };
            return request;
          });

          const batchRequest = {
            batch_id: this.selectedBatch,
            applications: applicationsToBeBatched
          };

          this.isAddingApplications = true;

          AddApplicationsToBatch(batchRequest)
            .then(response => {
              this.isAddingApplications = false;
              this.addedToBatch = true;

              // Refresh the stockbroker data
              SaveStockbrokerInfoToStore(this.companyID);
              SaveBatchesOwnedByStockbrokersToStore(this.companyID);

              this.cancelAddApplicationsDialog();
            })
            .catch(err => {
              this.errorMessage = err.response.data.message;
              this.isAddingApplications = false;
              this.addingToBatchFailed = true;
            });
        }
      });
    },

    cancelAddApplicationsDialog() {
      this.showAddApplicationsToBatchDialog = false;
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



