<template>
    
</template>


<script>
import ConfigData from "../../../../services/ConfigDataService";

export default {
  /**
   * Redirect to EIPO applicant module before the initiate application
   * page mounts.
   * I needed to go this route, because vue's router cannot navigate to a page on a totally
   * different application from the one vue is powering (or at least I dont know how to yet).
   */
  beforeMount() {
    const win = window.open(ConfigData.applicantModuleURL, "_blank");
    if (win) {
      // Browser allows popups to be opened
      win.focus();

      // Navigate to the pending applications page.
      this.$router.push({ name: "pending-applications" });
    } else {
      // Browser blocked popup
      alert("Please allow popups for this application to function properly");
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



