<!-- 
    Component which display a loading spinner and text, to provide visual feedback to users.
    This is especially useful for providing feedback on asynchronous requests
-->
<template>
    <v-layout  row class=" d-flex justify-center ">
        <v-flex class="xs6 d-flex justify-center align-center">
            <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
            <span class="font-size-12 ml5">{{loadingText}}</span>
        </v-flex>
    </v-layout>
</template>


<script>
export default {
  props: {
    loadingText: String,
    default: "Loading..."
  }
};
</script>


<style lang="sass">
</style>



