<template>
    <div> 
        <v-dialog v-model="showViewApplicationDialog" fullscreen transition="dialog-bottom-transition" :overlay=false>
          <!--Button used to trigger the opening of the edit user dialog -->
          <v-btn id="openViewApplicationDialog"
          style="display: none" color="primary" dark slot="activator" ref="triggerButton">
          </v-btn>

          <v-card>
            <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px"> APPLICATION DETAILS</h5>
            </v-card-title>
              <!-- Content -->
            <v-card-text>

                <!-- Buttons -->
                <v-container fluid class="p0 ml0">
                    <v-layout  row class="mt0 mb20" text-right justify-end>
                        <!-- <v-flex xs6 sm6> -->
                           <v-btn small class="red darken-1 white--text"
                        @click="cancelViewApplication()">
                        CLOSE
                        </v-btn>
                        
                    </v-layout>
                </v-container>

                <!-- Editing Form -->
                <v-layout v-if="application.application_type_id">
                    <v-form class="w100p">
                      <v-layout row justify-centerr class="d-flex pl15 pr15">
                        <!-- Company Name -->
                        <v-flex xs12 sm6 v-if="isCorporateApplication" class="height-55px mb6">
                            <v-text-field   :label="'COMPANY NAME'" v-model="application.corporate.companyName" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" 
                            name="application.companyName" :disabled="true">
                            </v-text-field>
                        </v-flex>

                        <!-- Company contact person Name -->
                        <v-flex xs12 sm6 v-if="isCorporateApplication" class="height-55px mb6">
                            <v-text-field   :label="'CONTACT PERSON'" v-model="application.contactPerson" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" 
                            name="application.contactPerson" :disabled="true">
                            </v-text-field>
                        </v-flex>

                         <!-- Email -->
                        <v-flex xs12 sm6 class="height-55px mb6">
                            <v-text-field   :label="'EMAIL'" v-model="application.email" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.email" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>

                        <!-- Names -->
                        <template v-if="!isCorporateApplication">
                            <template v-for="(applicant, index) in application.applicants">
                                <v-flex xs12 sm6 class="height-55px mb6" :key="index">
                                    <v-text-field   :label="`FIRST NAME ${index+1}`" v-model="applicant.firstname" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicant.firstname" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 class="height-55px mb6" :key="index">
                                    <v-text-field   :label="`LAST NAME ${index+1}`" v-model="applicant.lastname" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicant.lastname" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 class="height-55px mb6" :key="index">
                                    <v-text-field   :label="`OTHER NAMES ${index+1}`" v-model="applicant.other_names" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicant.other_names" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                
                            </template>
                        </template>

                        <v-flex xs12 class="mt20"><b>CONTACT & ADDRESS</b></v-flex>

                        <!-- Address Street -->
                        <v-flex  xs12 class="height-55px mb6">
                            <v-text-field   :label="'STREET'" v-model="application.address.address" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.address.address" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>

                        <!-- Address City -->
                        <v-flex  xs12 class="height-55px mb6">
                            <v-text-field   :label="'CITY'" v-model="application.address.city" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.address.city" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>

                        <!-- Address State -->
                        <v-flex  xs12 class="height-55px mb6">
                            <v-text-field   :label="'STATE'" v-model="application.address.state" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicantion.address.state" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>

                        <!-- Phone number -->
                        <v-flex  xs12 class="height-55px mb6">
                            <v-text-field   :label="'PHONE NUMBER'" v-model="application.applicants[0].phone" 
                            :type="'number'" :rules="requiredRule"  v-validate="'required'" name="application.applicants[0].phone" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>

                        <v-flex xs12 class="mt20"><b>NEXT OF KIN</b></v-flex>

                        <!-- Next Of Kin Name -->
                        <v-flex  xs12 class="height-55px mb6" v-if="!isCorporateApplication">
                            <v-text-field   :label="'NEXT OF KIN'" v-model="application.applicant_details[0].nok_name" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.applicant_details[0].nok_name" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>

                          <!-- NOK Phone number -->
                        <v-flex  xs12 class="height-55px mb6" v-if="!isCorporateApplication">
                            <v-text-field   :label="'PHONE NUMBER'" v-model="application.applicant_details[0].nok_phone" 
                            :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.applicant_details[0].applicant_details.nok_phone" 
                            :disabled="true">
                            </v-text-field>
                        </v-flex>
                      </v-layout>

                        <v-flex xs12 class="mt20"><b>INVESTMENT & ALLOTMENT DETAILS</b></v-flex>

                        <!-- Bids -->
                        <template v-if="application.ipo_request.bids">
                            <template v-for="(bid, index) in application.ipo_request.bids">
                                <v-layout row class="w100p" :key="index">
                                    <!-- Bid share -->
                                    <v-flex  xs4 class="height-55px mb6 d-flex" align-items-baseline :key="index">
                                        <span class="mr15"><b>{{ index+1 }}</b></span>
                                        <v-text-field   :label="'NO OF SHARES'" v-model="bid.bid_share" 
                                        :type="'number'" :rules="requiredRule"  v-validate="'required'" name="bid.bid_share" 
                                        :disabled="true">
                                        </v-text-field>
                                    </v-flex>
                                    <v-flex  xs4 class="height-55px mb6" :key="index">
                                        <v-text-field   :label="'BID PRICE'" v-model="bid.bid_price" 
                                        :type="'number'" :rules="requiredRule"  v-validate="'required'" name="bid.bid_price" 
                                        :disabled="true">
                                        </v-text-field>
                                    </v-flex>
                                    <v-flex  xs4 class="height-55px mb6" :key="index">
                                        <v-text-field   :label="'AMOUNT '" v-model="bid.bid_amount" 
                                        :type="'number'" name="bid.bid_amount" 
                                        :disabled="true">
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>
                            </template>
                        </template>

                        <!-- Investor Details -->
                        <template v-if="application.ipo_request">
                            <v-flex xs12 class="mt20"><b>INVESTOR DETAILS</b></v-flex>
                            <v-layout row class="w100p pl15">
                                <!-- CSCS Acc -->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'INVESTOR\'S CSCS ACCOUNT NO'" v-model="application.ipo_request.cscs_account" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.ipo_request.cscs_account" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!--  Clearing House No-->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'CLEARING HOUSE NO (CHN)'" v-model="application.ipo_request.chn" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.ipo_request.chn" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                            </v-layout>
                        </template>

                         <!-- Bank Details -->
                         <template v-if="application.bank_detail">
                            <v-flex xs12 class="mt20"><b>BANK DETAILS</b></v-flex>
                            <v-layout row class="w100p pl15">
                                <!-- BANK NAME -->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'BANK NAME'" v-model="application.bank_detail.bank_name" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bankName" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!-- BANK  ACCOUNT NAME -->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'ACCOUNT NAME'" v-model="application.bank_detail.account_name" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bank_detail.account_name" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!-- ACCOUNT NUMBER-->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'ACCOUNT NUMBER'" v-model="application.bank_detail.account_number" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bank_detail.account_number" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!-- BVN-->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'BVN'" v-model="application.bank_detail.bvn" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bank_detail.bvn" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!--  SORT CODE -->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'SORT CODE'" v-model="application.bank_detail.sort_code" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bank_detail.sort_code" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!--  BANK CITY -->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'CITY'" v-model="application.bank_detail.city" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bank_detail.city" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                                <!--  BANK STATE -->
                                <v-flex  xs6 sm6 class="height-55px mb6 d-flex" align-items-baseline>
                                    <v-text-field   :label="'STATE'" v-model="application.bank_detail.state" 
                                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="application.bank_detail.state" 
                                    :disabled="true">
                                    </v-text-field>
                                </v-flex>
                            </v-layout>
                         </template>

                    </v-form>
                </v-layout>

                <!-- Buttons -->
                <v-container fluid class="p0 ">
                    <v-layout  row class="mt20">
                    <v-flex xs12 sm4 xs6>
                        <v-btn small block class="red darken-1 white--text"
                        @click="cancelViewApplication()">
                        CLOSE
                        </v-btn>
                    </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
          </v-card>
      </v-dialog>

    </div>

</template>


<script>
import { EApplicationTypeId } from "../../../../../models/index";
export default {
  props: {
    application: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      isViewingApplication: false,
      showViewApplicationDialog: false,
      valid: false,
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    isIndividualApplication() {
      return (
        this.application.application_type_id === EApplicationTypeId.Individual
      );
    },
    isJointApplication() {
      return this.application.application_type_id === EApplicationTypeId.Joint;
    },
    isCorporateApplication() {
      return (
        this.application.application_type_id === EApplicationTypeId.Corporate
      );
    }
  },

  methods: {
    cancelViewApplication: function() {
      // Close the dialog modal
      this.showViewApplicationDialog = false;
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



