/**
 * This config file houses various config data needed for the application to work
 */

export default {
  // Role id for agents
  agentRoleID: 4,

  // The ID of ipo's should be programmatic, but this is used for now
  defaultIpoID: 1,

  // Default id for the stocbroker company
  defaultCompanyID: 1,

  // Default username used to create agents
  defaultAgentUsername: "defaultAgentUsername",

  // Default password used to create agents
  defaultAgentPassword: function() {
    return "defaultAgentPassword";
  },

  // Url for the applicant module of the EIPO application.
  applicantModuleURL: "https://eipo-applicant.cardinalstone.com/"
};



