<script>
// import Batches from "../../../../data-mocks/eipo/batches.mock";
import ViewBatch from "./dialogs/ViewBatch";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      search: "",
      batch: {},
      showViewBatchDialog: false,
      selected: [],
      loadingDataTable: false,
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }]
    };
  },

  components: {
    ViewBatch
  },

  computed: {
    ...mapGetters({
      batches: "batches"
    })
  },

  methods: {
    /**
     * Used to toggle the select all functionality
     */
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else this.selected = this.applications.slice();
    },

    /**
     * Show the dialog box for various batch specific actions, depending on what action the user selects.
     */
    showBatchDialog(type, batch, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.batch = batch;

      // Display the edit popup modal/dialog
      if (type === "VIEW") {
        document.querySelector("#openViewBatchDialog").click();
      }
      // else if (type === "DELETE") {
      //   document.querySelector("#openApproveApplicationDialog").click();
      // } else if (type === "SUBMIT") {
      //   document.querySelector("#openEditApplicationDialog").click();
      // }

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    showSubmitBatchDialog() {},

    /**
     * Submit a batch
     */
    submitBatch(batch) {}
  }
};
</script>


