import {
  pieChartDummyData,
  barChartDummyData,
  ipoVolumeBarChartDummyData
} from "../../../../data-mocks/eipo/graphs";
import { ChartService } from "../../../../services/ChartService";
import { IApplication } from "../../../../models";
import { GetFormattedApplicantName } from "./agent.transformers";

const getters = {
  /**
   * Get the ID of the authenticated agent.
   */
  getAgentID: (state, getters) => {
    return state.user.id;
  },

  /**
   * Get the ID of the stockbroking company which the authenticated user belongs to.
   */
  getCompanyID: (state, getters) => {
    return state.company.id;
    // return state.companyID;
  },

  getUsername: state => {
    return state.user.first_name + " " + state.user.other_names;
  },

  /**
   * Get the stockbroker company's agents
   */
  getAgents: (state, getters) => {
    let users = state.agents ? state.agents : [];

    users = users.map(user => {
      user.fullName = user.first_name + " " + user.other_names;
      return user;
    });

    return users;
  },

  // Get all applications made to the authenticated broker company
  allApplications: (state, getters) => {
    let applications = state.applications ? state.applications : [];

    // Get the properly formatted applicant name for the applications
    applications = applications.map((application: IApplication) => {
      application.formattedApplicantName = GetFormattedApplicantName(
        application
      );
      return application;
    });

    return applications;
  },

  // Get all pending applications made to the authenticated stockbroker company
  pendingApplications: (state, getters) => {
    const allApplications = getters.allApplications;

    let pendingApplications = allApplications.filter(
      (application: IApplication) =>
        application.ipo_request.process_status === "PENDING"
    );

    return pendingApplications;
  },

  // Get all accepted applications (which can be batched) made to the authenticated stockbroker company
  approvedApplications: (state, getters) => {
    const allApplications = getters.allApplications;

    let acceptedApplications = allApplications.filter(
      (application: IApplication) =>
        application.ipo_request.process_status === "ACCEPTED" &&
        !application.batched
    );

    return acceptedApplications;
  },

  // Get all rejected applications made to the authenticated stockbroker company
  rejectedApplications: (state, getters) => {
    const allApplications = getters.allApplications;

    let rejectedApplications = allApplications.filter(
      (application: IApplication) =>
        application.ipo_request.process_status === "REJECTED"
    );

    return rejectedApplications;
  },

  // Get all approved applications made to the authenticated stockbroker company
  submittedApplications: (state, getters) => {
    const allApplications = getters.allApplications;

    let submittedApplications = allApplications.filter(
      (application: IApplication) =>
        application.ipo_request.process_status === "SUBMITTED"
    );

    return submittedApplications;
  },

  // Get a list of all batches
  batches: (state, getters) => {
    let batches = state.batches ? state.batches : [];
    batches.forEach(batch => {
      if (batch.submitted) {
        batch.status_text = "SUBMITTED";
      } else {
        batch.status_text = "PENDING";
      }
    });
    return batches;
  },

  // Get the data to be plotted as the pie chart on the dashboard of the agent
  applicationTypeSummaryPieChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data.
    const chartData = ChartService.getCspPieChart(pieChartDummyData);
    return chartData;
  },

  // Get the data used to plot the bar chart showing application statuses (Pending, approved and rejected)
  applicationStatusBarChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data.
    const chartData = ChartService.getCspBarChart(barChartDummyData);
    return chartData;
  },

  // Get the data used to plot the ipo volume summary
  ipoVolumeSummaryChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data.
    const chartData = ChartService.getCspBarChart(ipoVolumeBarChartDummyData);
    return chartData;
  },

  batchStatusChartData: (state, getters) => {
    // Note the this data is currently a mock, and should be replaced with proper data.
    const chartData = ChartService.getCspBarChart(barChartDummyData);
    return chartData;
  }
};

export default getters;



