import getters from "./agent.getters";
import mutations from "./agent.mutations";

import * as mutationTypes from "../../../mutation-types";

// Initial Store State
export const state = () => ({
  applications: [],
  agents: [],
  batches: [],
  company: {},
  user: {},
  role: {}
});

const allMutations = {
  ...mutations,

  /**
   * Reset the agent module store to its initial state when logging out a user
   * @param state
   */
  [mutationTypes.CLEAR_AGENT_MODULE_STORE](currentState) {
    const initialState = state();
    Object.keys(initialState).forEach(key => {
      currentState[key] = initialState[key];
    });
  }
};

export default {
  state: state(),
  getters,
  mutations: allMutations
};



