export enum EGender {
  "Male" = "MALE",
  "Female" = "FEMALE"
}

export enum EUserRole {
  "super-admin" = 1,
  "applicants" = 2,
  "stock-broker-company" = 3,
  "agents" = 4,
  "issuing-house" = 5,
  "registrar" = 6
}

export interface IUser {
  id: number;
  role_id: EUserRole;
  first_name: string;
  other_name?: string;
  last_name?: string;
  gender: EGender;
  phone: string;
  email: string;
  firstLogin: boolean;
}

export interface IAgent extends IUser {
  company_id: number;
}



