<template>
  <v-container fluid class="pt0">
    <!-- Add user button -->
    <v-card class="elevation-0 mb0 row d-flex flex-column ml1 mr1"
      style="background: transparent; border: none">
      <v-container fluid class="pl1 pr1 pt2">
         <v-progress-linear v-if="fetchingUsers" v-bind:indeterminate="fetchingUsers"></v-progress-linear>
        <v-layout row>
          <v-flex xs5 class="d-flex align-items-end">
             <h3 class="csp-light-blue-text font-size-20">Agent Users</h3>
          </v-flex>

          <v-flex xs7 d-flex class="">
            <v-container fluid class="p0">
              <v-layout row class="">
                <v-flex xs6>
                </v-flex>
                <v-flex d-flex xs6 class="">
                    <v-btn :to="'/ipo/agent/addUser'" info  class="fund-account-btn font-size-12 p0 ml-auto mb0 mr0">
                        Add User
                    </v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
    
    <!-- Table -->
    <v-card class="elevation-0 mb10">
      <div class="heightt-300px " style="overflow-y: scroll"> <!-- Used so the market data table scrolls -->
         <v-data-table
        hide-actions
        :items="users"
        :search="search"
        :loading="loadingDataTable"
        :rows-per-page-items="rowsPerPageItems"
        class="elevation-1 marketDataTable table-striped table-bordered">
        <template slot="headers" scope="props">
          <tr class="bg-csp-light-blue marketDataTableHeader">
            <th class="text-left">AGENT NAME</th>
            <!-- <th class="text-left">OTHER NAME</th>
            <th class="text-left">LAST NAME</th> -->
            <th class="text-left">EMAIL</th>
            <th class="text-left">PHONE NUMBER</th>
            <th class="text-center width-80px white--text ">ACTIONS</th>
          </tr>
        </template>

        <template slot="items" scope="props">
          <tr>
              <td>{{ props.item.fullName }}</td>
              <!-- <td>{{ props.item.other_names }}</td>
              <td>{{ props.item.last_name }}</td> -->
              <td>{{ props.item.email }}</td>
              <td>{{ props.item.phone }}</td>
              <td class="font-size-10 text-center width-80px d-flex align-center justify-center" style="padding: 0px; border: 0px">
                <!-- EDIT USER -->
               <v-btn
                data-toggle="tooltip" data-placement="top" title="EDIT" icon class="m0 p0 width-30px height-30px"
                @click="showEditUser(props.item, $event)">
                <v-icon  class="blue--text font-weight-bold font-size-18" >
                 mode_edit
                </v-icon>
               </v-btn>
               <!-- VIEW USER -->
                <v-btn
                  data-toggle="tooltip" data-placement="top" title="VIEW"
                  icon class="m0 p0 width-30px height-30px"
                  @click="showViewUser(props.item, $event)">
                  <v-icon class="color-csp-blue ml4 fa fa-eye fa-2x btn-icon font-weight-bold font-size-18">
                  </v-icon>
                </v-btn>
                <!-- DELETE USER -->
                <v-btn
                  data-toggle="tooltip" data-placement="top" title="DELETE USER"
                  icon class="m0 p0 width-30px height-30px"
                  @click="showDeleteUser(props.item, $event)">
                  <v-icon  class="red--text font-weight-bold font-size-18" >
                    close
                  </v-icon>
                </v-btn>
              </td>
          </tr>
        </template>
      </v-data-table>

      </div>
    </v-card>

    <!-- Dialog for updating users -->
    <v-dialog v-model="showUpdateUserDialog" lazy absolute>
      <!--Button used to trigger the opening of the edit user dialog -->
      <v-btn id="openUpdateUserDialog"
        style="display: none" color="primary" dark slot="activator">
      </v-btn>

      <v-card>
        <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
          <h5 class="white--text font-size-20px">UPDATE USER DATA</h5>
        </v-card-title>
        <!-- Form -->
        <v-card-text>
          <v-form v-model="valid" ref="form" class="w100p pl5">

            <!-- First Name -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'FIRST NAME'"
              v-model="user.first_name"
              :type="'text'"
              name="firstName">
             </v-text-field>
            </v-flex>

             <!-- Last Name -->
            <!-- <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'LAST NAME'"
              v-model="user.last_name"
              :type="'text'"
              name="lastName">
             </v-text-field>
            </v-flex> -->

            <!-- Other Name -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'OTHER NAMES'"
              v-model="user.other_names"
              :type="'text'"
              name="otherName">
             </v-text-field>
            </v-flex>

             <!-- Email -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'EMAIL'"
              v-model="user.email"
              :type="'text'"
              name="email">
             </v-text-field>
            </v-flex>

            <!-- Phone Number -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'PHONE NUMBER'"
              v-model="user.phone"
              :type="'text'"
              name="phoneNumber">
             </v-text-field>
            </v-flex>

            <!-- Buttons -->
            <v-container fluid class="p0 ">
              <!-- Loading text -->
              <v-layout row>
                <v-flex xs12>
                  <!-- Loading Icon when adding a user -->
                  <loading v-if="isUpdatingUser" :loadingText="'Updating the User...'" class="mt20"></loading>
                </v-flex>
              </v-layout>
              <v-layout  row class="mt20">
                <v-flex xs6 class="d-flex justify-end ">
                  <v-btn small 
                    style="background: #4c7396; color: #FFFFFF"
                      :loading="isUpdatingUser"
                      :disabled="isUpdatingUser"
                    @click="updateUser()">
                    UPDATE
                  </v-btn>
                </v-flex>

                <v-flex xs6>
                  <v-btn small class="red darken-1 white--text"
                    @click="cancelUserUpdateForm">
                    CANCEL
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>

          </v-form>
        </v-card-text>

      </v-card>
    </v-dialog>

     <!-- Dialog for viewing users -->
    <v-dialog v-model="showViewUserDialog" lazy absolute>
      <!--Button used to trigger the opening of the edit user dialog -->
      <v-btn id="openViewUserDialog"
        style="display: none" color="primary" dark slot="activator">
      </v-btn>

      <v-card>
        <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
          <h5 class="white--text font-size-20px">USER DATA</h5>
        </v-card-title>
        <!-- Form -->
        <v-card-text>
          <v-form v-model="valid" ref="form" class="w100p pl5">

            <!-- First Name -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'FIRST NAME'"
              v-model="user.first_name"
              :type="'text'"
              name="firstName" disabled>
             </v-text-field>
            </v-flex>

             <!-- Last Name -->
            <!-- <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'LAST NAME'"
              v-model="user.last_name"
              :type="'text'"
              name="lastName" disabled>
             </v-text-field>
            </v-flex> -->

            <!-- Other Name -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'OTHER NAMES'"
              v-model="user.other_names"
              :type="'text'"
              name="otherName" disabled>
             </v-text-field>
            </v-flex>

             <!-- Email -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'EMAIL'"
              v-model="user.email"
              :type="'text'"
              name="email" disabled>
             </v-text-field>
            </v-flex>

            <!-- Phone Number -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'PHONE NUMBER'"
              v-model="user.phone"
              :type="'text'"
              name="phoneNumber" disabled>
             </v-text-field>
            </v-flex>

            <!-- Buttons -->
            <v-container fluid class="p0 ">
              <v-layout  row class="mt20">
                <v-flex xs6>
                  <v-btn small class="red darken-1 white--text"
                    @click="cancelUserViewForm">
                    CANCEL
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>

          </v-form>
        </v-card-text>

      </v-card>
    </v-dialog>

    <!-- Dialog for deleting users -->
    <v-dialog v-model="showDeleteUserDialog" lazy absolute>
       <!--Button used to trigger the opening of the delete user dialog -->
      <v-btn id="openDeleteUserDialog"
        style="display: none" color="primary" dark slot="activator">
      </v-btn>

      <v-card>
        <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
            <h5 class="white--text font-size-20px">DELETE USER</h5>
        </v-card-title>
        <v-card-text>
          <p>Are you sure you want to delete this user?</p>

          <!-- Buttons -->
          <v-container fluid class="p0">
            <!-- Loading text -->
            <v-layout row>
              <v-flex xs12>
                <!-- Loading Icon when adding a user -->
                <loading v-if="isDeletingUser" :loadingText="'Deleting the User...'" class="mt20"></loading>
              </v-flex>
            </v-layout>
            <v-layout  row class="mt20">
              <v-flex xs6 class="d-flex justify-end ">
                <v-btn small 
                  style="background: #4c7396; color: #FFFFFF"
                    :loading="isDeletingUser"
                    :disabled="isDeletingUser"
                  @click="deleteUser(user)">
                  YES DELETE
                </v-btn>
              </v-flex>

              <v-flex xs6>
                <v-btn small class="red darken-1 white--text"
                  @click="cancelUserDeleteDialog">
                  CANCEL
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>

    <!-- Snackbar to show successful user updates -->
    <v-snackbar error
      :timeout="snackbarTimeout"
      :top="true"
      :color="'error'"
      v-model="userUpdateFailed">
      User update failed
    </v-snackbar>
    
    <!-- Snackbar showing unsuccessful user updates -->
    <v-snackbar success
      :timeout="snackbarTimeout"
      :top="true"
      :color="'success'"
      v-model="userUpdated">
      User has been successfully updated
    </v-snackbar>

    <!-- Snackbar to show successful user deletes -->
    <v-snackbar error
      :timeout="snackbarTimeout"
      :top="true"
      :color="'error'"
      v-model="userDeleteFailed">
      User delete failed
    </v-snackbar>
    
    <!-- Snackbar showing unsuccessful user deletes -->
    <v-snackbar success
      :timeout="snackbarTimeout"
      :top="true"
      :color="'success'"
      v-model="userDeleted">
      User has been successfully deleted
    </v-snackbar>

  </v-container>
</template>


<script>
import { mapGetters } from "vuex";
import { UpdateUser, DeleteUser } from "../../../../services/UserService";
import { SaveStockbrokerInfoToStore } from "../../../../services/MutationServices";
import Loading from "../../../shared/Loading";

export default {
  components: {
    Loading
  },
  data() {
    return {
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }],
      search: "",
      loadingDataTable: false,
      fetchingUsers: false,
      showUpdateUserDialog: false,
      showViewUserDialog: false,
      showDeleteUserDialog: false,
      valid: false,
      user: {},
      snackbarTimeout: 4000,
      userUpdateFailed: false,
      userUpdated: false,
      userDeleted: false,
      userDeleteFailed: false,
      isUpdatingUser: false,
      isDeletingUser: false
    };
  },

  computed: {
    ...mapGetters({
      users: "getAgents",
      user_id: "getAgentID",
      companyID: "getCompanyID"
    })
  },

  methods: {
    /**
     * Format the list of users gotten from the API to a structure that fits what the frontend requires
     */
    formatResponseToFitFrontEnd: function(apiFormattedUsers) {
      let users = apiFormattedUsers.map(user => {
        return {
          firstName: user.first_name,
          lastName: user.last_name,
          otherName: user.other_names,
          email: user.email,
          phoneNumber: user.phoneNumber
        };
      });

      return users;
    },

    /**
     * Show the dialog used to edit a user
     */
    showEditUser: function(user, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.user = user;

      // Display the edit popup modal/dialog
      document.querySelector("#openUpdateUserDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    /**
     * Show the dialog which presents a readonly view of a user's profile
     */
    showViewUser: function(user, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.user = user;

      // Display the edit popup modal/dialog
      document.querySelector("#openViewUserDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    /**
     * Show the confirmation dialog before deleting a user
     */
    showDeleteUser: function(user, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.user = user;

      // Display the edit popup modal/dialog
      document.querySelector("#openDeleteUserDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    /**
     * Update the details for a user
     */
    updateUser: function() {
      this.isUpdatingUser = true;

      UpdateUser(this.user)
        .then(response => {
          this.userUpdated = true;
          this.isUpdatingUser = false;

          // Refresh the list of users
          SaveStockbrokerInfoToStore(this.companyID);
          this.cancelUserUpdateForm();
        })
        .catch(error => {
          console.log(error);
          this.userUpdateFailed = true;
          this.isUpdatingUser = false;
        });
    },

    /**
     * Delete a user
     */
    deleteUser: function(user) {
      this.isDeletingUser = true;

      DeleteUser(user.id)
        .then(response => {
          this.userDeleted = true;
          this.isDeletingUser = false;
          this.cancelUserDeleteDialog();

          // Refresh the list of users
          SaveStockbrokerInfoToStore(this.companyID);
        })
        .catch(error => {
          console.log(error);
          this.userDeleteFailed = true;
          this.isDeletingUser = false;
        });
    },

    /**
     * Dismiss the update user popup
     */
    cancelUserUpdateForm: function() {
      // Close the dialog modal
      this.showUpdateUserDialog = false;

      // Clear the selected user
      this.user = {};
    },

    /**
     * Dismiss the view user form
     */
    cancelUserViewForm: function() {
      // Close the dialog modal
      this.showViewUserDialog = false;

      // Clear the selected user
      this.user = {};
    },

    /**
     * Dismiss the delete user confirm dialog
     */
    cancelUserDeleteDialog: function() {
      // Close the dialog modal
      this.showDeleteUserDialog = false;

      // Clear the selected user
      this.user = {};
    }
  }
};
</script>


<style scoped lang="sass">
  .fund-account-btn
    background: #324e85 !important
    border-radius: 5px
    border: none
    padding: 5px 10px !important
  .marketDataTable
    margin-bottom: 0px !important

  .marketDataTableHeader,
  .marketDataTableBody
    height: 25px

  .marketDataTableHeader
    th
      padding: 5px 5px !important
      color: #FFFFFF !important
      font-weight: bold
      font-size: 10px

  .marketDataTableBody
    border-bottom: none !important
    &:hover
      background: #CCC !important
      cursor: pointer

  .marketDataTableBody
    td
      padding: 5px 5px !important
      height: 25px
      color: #31708f
    td:last-child
      border-bottom: 0px solid transparent !important
  .btn__content
    padding: 0px !important
  .eemulate-link
    &:hover
      transition: all .2s
      text-decorations: underline
      border-bottom: 2px solid #1a2155
      padding-bottom: 2px
      cursor: pointer
      background: #EEEEEE

  .selectedRow
    background: #b0bed9 !important
    font-weight: bold
</style>



