<template>
   <v-container fluid class="pt0">
    
    <!-- Page title and search -->
    <v-card class="elevation-0 mb0 row d-flex flex-column ml1 mr1"
      style="background: transparent; border: none">
      <v-container fluid class="pl1 pr1 pt2">
        <v-layout row>
          <v-flex xs5 class="d-flex align-items-end">
             <h3 class="csp-light-blue-text font-size-20">Reports</h3>
          </v-flex>

          <v-flex xs7 d-flex class="">
            <v-container fluid class="p0">
              <v-layout row class="">
                <v-flex xs6>

                </v-flex>
                <v-flex d-flex xs6 class="">
                  <v-text-field
                    append-icon="search"
                    label="SEARCH"
                    hide-details
                    class="xs4 font-size-11"
                    v-model="search">
                  </v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>

    <!-- Table -->
    <v-card class="elevation-0 mb10">
      <div class="heightt-300px " style="overflow-y: scroll"> <!-- Used so the market data table scrolls -->
         <v-data-table
        hide-actions
        :items="reports"
        :search="search"
        :loading="loadingDataTable"
        :rows-per-page-items="rowsPerPageItems"
        class="elevation-1 marketDataTable table-striped table-bordered">
        <template slot="headers" scope="props">
          <tr class="bg-csp-light-blue marketDataTableHeader">
           <th class="text-left">REPORT NAME</th>
            <th class="text-center width-80px white--text ">ACTIONS</th>
          </tr>
        </template>

        <template slot="items" scope="props">
          <tr>
              <td>{{props.item.name}}</td>
              <td class="font-size-10 text-center width-100px d-flex align-center justify-center" style="padding: 0px; border: 0px">
                <!-- BUY -->
              
               <!-- WATCH -->
                <v-btn primary
                  data-toggle="tooltip" data-placement="top" title="WATCHLIST"
                   class="fund-account-btn mt5 mb5 p0 width-30px height-30px"
                  @click="watchStock(props.item.name)">
                  VIEW
                </v-btn>
              </td>
          </tr>
        </template>
      </v-data-table>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import MockReports from "../../../../data-mocks/eipo/reports.mock";

export default {
  data() {
    return {
      reports: MockReports,
      search: "",
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }],
      loadingDataTable: false
    };
  }
};
</script>

<style scoped lang="sass">
  .fund-account-btn
    background: #324e85 !important
    border-radius: 5px
    border: none
    padding: 5px 5px !important
    margin: 5px !important
  .marketDataTable
    margin-bottom: 0px !important

  .marketDataTableHeader,
  .marketDataTableBody
    height: 35px

  .marketDataTableHeader
    th
      padding: 5px 5px !important
      color: #FFFFFF !important
      font-weight: bold
      font-size: 10px

  .marketDataTableBody
    border-bottom: none !important
    &:hover
      background: #CCC !important
      cursor: pointer

  .marketDataTableBody
    td
      padding: 10px 10px !important
      height: 35px
      color: #31708f
    td:last-child
      border-bottom: 0px solid transparent !important
  .btn__content
    padding: 0px !important
  .eemulate-link
    &:hover
      transition: all .2s
      text-decorations: underline
      border-bottom: 2px solid #1a2155
      padding-bottom: 2px
      cursor: pointer
      background: #EEEEEE

  .selectedRow
    background: #b0bed9 !important
    font-weight: bold
</style>


