<script>
import { EditApplication } from "../../../../../services/ApplicationService";
import { SaveStockbrokerInfoToStore } from "../../../../../services/MutationServices";
import { mapGetters } from "vuex";
export default {
  props: {
    application: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      applicationEdited: false,
      applicationEditingFailed: false,
      isEditingApplication: false,
      showEditApplicationDialog: false,
      valid: false,
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    ...mapGetters({
      userID: "getAgentID",
      companyID: "getCompanyID"
    }),

    isIndividualApplication() {
      return this.application.applicationType === 1;
    },
    isJointApplication() {
      return this.application.applicationType === 2;
    },
    isCorporateApplication() {
      return this.application.applicationType === 3;
    },
    amount_1() {
      if (
        this.application.bids[0].bid_share &&
        this.application.bids[0].bid_price
      ) {
        const amount =
          parseFloat(this.application.bids[0].bid_share) *
          parseFloat(this.application.bids[0].bid_price);

        this.application.bids[0].bid_amount = amount.toFixed(2);
        return amount.toFixed(2);
      }
    },
    amount_2() {
      if (
        this.application.bids[1].bid_share &&
        this.application.bids[1].bid_price
      ) {
        const amount =
          parseFloat(this.application.bids[1].bid_share) *
          parseFloat(this.application.bids[1].bid_price);

        this.application.bids[1].bid_amount = amount.toFixed(2);
        return amount.toFixed(2);
      }
    },
    amount_3() {
      if (
        this.application.bids[2].bid_share &&
        this.application.bids[2].bid_price
      ) {
        const amount =
          parseFloat(this.application.bids[2].bid_share) *
          parseFloat(this.application.bids[2].bid_price);

        this.application.bids[2].bid_amount = amount.toFixed(2);
        return amount.toFixed(2);
      }
    }
  },

  methods: {
    /**
     * Show the confirmation dialog box before approving an application
     */
    showEditApplication(application, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.application = application;

      // Display the edit popup modal/dialog
      this.$refs.triggerButton.click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    /**
     * Used to reject an application
     */
    editApplication(applicationId) {
      this.$validator.validateAll().then(result => {
        // The form is invalid
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.editApplicationForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          // Send the rejection request to the server

          //   const request = {
          //     user_id: this.userID,
          //     application_id: applicationId,
          //     token: this.application.token,
          //     application: this.application
          //   };
          console.log(this.application);

          this.isEditingApplication = true;

          EditApplication(this.application)
            .then(response => {
              console.log(response);
              this.isEditingApplication = false;
              this.applicationEdited = true;

              // Refresh the applications
              SaveStockbrokerInfoToStore(this.companyID);

              // Hide the dialog box
              this.cancelEditApplication();
            })
            .catch(err => {
              console.log(err);
              this.isEditingApplication = false;
              this.applicationEditingFailed = true;
            });
        }
      });
    },

    cancelEditApplication: function() {
      // Close the dialog modal
      this.showEditApplicationDialog = false;

      // Clear the selected user
      //   this.application = {};
    }
  }
};
</script>


