import { IApplication, EApplicationTypeId } from "../../../../models";

/**
 * This file houses transformers used to transform the data returned from the API to a format
 * utiliseable by the front end
 *
 */

/**
 * Get the formatted applicant name for an IPO application
 *
 * @param {IApplication} application
 * @return {string}
 */
export const GetFormattedApplicantName = (application: IApplication) => {
  let name = "";

  if (application.application_type_id === EApplicationTypeId.Corporate) {
    name = application.corporate ? application.corporate.companyName : "";
  } else {
    application.applicants.forEach((applicant, index) => {
      name += `${applicant.firstname} ${applicant.lastname}`;
      if (index + 1 !== application.applicants.length) {
        name += " & ";
      }
    });
  }

  return name;
};

/**
 * Transform the info about an application to a frontend useable single object tree
 */
export const TransformAPiApplicationData = data => {
  const isIndividualApplication = data.application.application_type_id === 1;
  const isJointApplication = data.application.application_type_id === 2;
  const isCorporateApplication = data.application.application_type_id === 3;

  let companyName = "";
  let contactPerson = "";
  let formattedApplicantName = "";

  if (isIndividualApplication) {
    const names = data.application.applicant;
    formattedApplicantName = `${names[0].lastname} ${names[0].firstname}`;
  }

  if (isJointApplication) {
    const names = data.application.applicant;
    formattedApplicantName = `${names[0].lastname} ${names[0].firstname} & ${
      names[1].lastname
    } ${names[1].firstname}`;
  }

  if (isCorporateApplication) {
    companyName = data.application.corporate.companyName;
    contactPerson = data.application.corporate.contactPerson;
    formattedApplicantName = companyName;
  }

  let transformedApplicationData = {
    application_id: data.application_id,
    application_date: data.created_at,
    address_city: data.application.address.city,
    address_state: data.application.address.state,
    address_street: data.application.address.address,
    appSignatures: data.application.signatures,
    applicationType: data.application.application_type_id,
    bankAccountName: data.application.bank_detail.account_name,
    bankAccountNumber: data.application.bank_detail.account_number,
    bankCity: data.application.bank_detail.city,
    bankName: data.application.bank_detail.bank_name,
    bankSortCode: data.application.bank_detail.sort_code,
    bankState: data.application.bank_detail.state,
    bids: data.bids,
    broker: data.stock_broker_id,
    bvn: data.application.bank_detail.bvn,
    chnNo: data.chn,
    companyName: companyName,
    contactPerson: contactPerson,
    cscsNo: data.cscs_account,
    email: data.application.email,
    formattedApplicantName: formattedApplicantName,
    ipo_id: data.application.ipo_id,
    process_status: data.process_status,
    phoneNumber: data.application.applicant[0].phone,
    names: data.application.applicant,
    nok_name: data.application.applicant_details[0].nok_name,
    nok_phoneNumber: data.application.applicant_details[0].nok_phone,
    token: data.application.token
  };

  return transformedApplicationData;
};



