<template>
    <v-container class="pt0 mt10"
    fluid>
        <v-layout row>
            <v-flex sm12 lg6 class="mb10">
                <!-- Card Housing entire add batch -->
                <v-card>
                    <v-card-title primary-title class="font-weight-bold p2 d-flex justify-start">
                        <h5 class=" pt10 font-weight-bold pl5 mb-0 font-size-11 text-uppercase">
                            <span class="font-size-17 font-weight-normal">CREATE A NEW BATCH</span>
                        </h5>
                    </v-card-title>
                    <v-card-media>
                        <v-container fluid class="pt0 pl5 pr5">
                            <v-layout row>
                                <v-form v-model="valid" ref="createBatchForm" class="w100p">
                                <!-- Batch Name -->
                                <v-flex xs6 class="height-55px mb6 pl10">
                                    <v-text-field  :label="'BATCH NAME'" v-model="batchName" :type="'text'" :rules="requiredRule"  
                                    v-validate="'required'" name="batchName" :disabled="inPreviewState">
                                    </v-text-field>
                                </v-flex>
                                
                                <!-- Batch Description -->
                                <v-flex class="pl10">
                                    <v-text-field
                                    name="batchDescription"
                                    v-model="batchDescription"
                                    label="BATCH DESCRIPTION"
                                    multi-line
                                    ></v-text-field>
                                </v-flex>

                                </v-form>
                            </v-layout>

                            <!-- Loading Icon when creating batches-->
                            <v-layout v-if="isCreatingBatch"  row class=" d-flex justify-center ">
                                <v-flex class="xs6 d-flex justify-center align-center">
                                <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                                <span class="font-size-12 ml5">Creating New Batch</span>
                                </v-flex>
                            </v-layout>

                            <!-- Buttons -->
                            <v-layout row class="mt20">
                                <v-btn style="background: #4c7396; color: #FFFFFF"
                                @click="createBatch()" :loading="isCreatingBatch" :disabled="isCreatingBatch">
                                CREATE BATCH
                                </v-btn>
                                <v-btn class="red darken-1 white--text" >
                                Cancel
                                </v-btn>
                            </v-layout>
                        </v-container>
                    </v-card-media>
                </v-card>
            </v-flex>
        </v-layout>

        <!-- Snackbar to show success message when batch is created -->
        <v-snackbar success
            :timeout="snackbarTimeout"
            :top="true"
            :color="'success'"
            v-model="batchCreated"
            >
           Batch has been created successfully
        </v-snackbar>

        <!-- Snackbar to show error message when batch creation fails -->
        <v-snackbar error
            :timeout="snackbarTimeout"
            :top="true"
            :color="'error'"
            v-model="batchCreationFailed"
            >
            {{ errorMessage }}
        </v-snackbar>
    </v-container>
</template>


<script>
import { mapGetters } from "vuex";
import { CreateBatch } from "../../../../services/BatchServices";
import ConfigData from "../../../../services/ConfigDataService";
import { SaveBatchesOwnedByStockbrokersToStore } from "../../../../services/MutationServices";

export default {
  data() {
    return {
      errorMessage: "Unable to create batch",
      snackbarTimeout: 4000,
      valid: false,
      inPreviewState: false,
      batchName: null,
      batchDescription: "",
      isCreatingBatch: false,
      batchCreated: false,
      batchCreationFailed: false,
      requiredRule: [v => !!v || "Required"],
      ipo_id: 1
    };
  },

  computed: {
    ...mapGetters({
      brokerID: "getAgentID",
      companyID: "getCompanyID"
    })
  },

  methods: {
    createBatch() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.createBatchForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          const batchRequest = {
            batch_name: this.batchName,
            description: this.batchDescription,
            user_id: this.brokerID,
            ipo_id: ConfigData.defaultIpoID,
            company_id: this.companyID
          };

          this.isCreatingBatch = true;

          CreateBatch(batchRequest)
            .then(response => {
              this.isCreatingBatch = false;
              this.batchCreated = true;

              this.$router.push({ name: "list-batches" });

              // Refresh the batches list
              SaveBatchesOwnedByStockbrokersToStore(this.companyID);
            })
            .catch(err => {
              console.log(err.response);
              this.errorMessage = err.response.data.message;
              this.isCreatingBatch = false;
              this.batchCreationFailed = true;
            });
        }
      });
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



