<template>

    <div> 
        <v-dialog v-model="showViewBatchDialog" fullscreen transition="dialog-bottom-transition" :overlay=false>
          <!--Button used to trigger the opening of the view batch dialog -->
          <v-btn id="openViewBatchDialog"
          style="display: none" color="primary" dark slot="activator" ref="triggerButton">
          </v-btn>

          <v-card>
            <v-card-title class="d-flex justify-space-between bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px"> BATCH DETAILS</h5>
                   <v-btn small class="red darken-1 white--text"
                        @click="cancelViewBatch()">
                        CLOSE
                  </v-btn>
            </v-card-title>
              <!-- Content -->
            <v-card-text>

              <!-- Batch Details -->
              <v-container v-if="batch" fluid class="p0">
                <div>
                  <ul class="list-group list-group-flush black--text">
                    <li class="list-group-item font-weight-bold pt5 pb5">BATCH NAME
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span>{{batch.batch_name}}</span>
                      </span>
                    </li>
                      <li class="list-group-item font-weight-bold pt5 pb5">BATCH DESCRIPTION
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span>{{batch.description}}</span>
                      </span>
                    </li>
                    <li class="list-group-item font-weight-bold pt5 pb5">NO OF APPLICATIONS IN BATCH
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span v-if="batch.contents">{{batch.contents.length}}</span>
                        <span v-else>0</span>
                      </span>
                    </li>
                    <li class="list-group-item font-weight-bold pt5 pb5">BATCH STATUS
                      <!-- Index Change -->
                      <span class="ml-auto font-weight-normal">
                        <span>{{batch.status_text}}</span>
                      </span>
                    </li>
                      
                  </ul>
                </div>
              </v-container>

              <!-- Batch Table -->
              <v-container fluid class="p0 mt40">

                  <v-btn small class="blue darken-1 white--text"
                        @click="viewBatchApplications($event)">
                        VIEW APPLICATIONS
                  </v-btn>
                  <v-btn v-if="!batch.submitted" small class="success darken-1 white--text"
                        @click="submitBatch(batch)"
                        :loading="isSubmittingBatch" :disabled="isSubmittingBatch">
                        SUBMIT BATCH
                  </v-btn>
                  <v-btn v-if="!batch.submitted" small class="error darken-1 white--text"
                        @click="deleteBatch(batch)" :loading="isDeletingBatch" :disabled="isDeletingBatch">
                        DELETE BATCH
                  </v-btn>
              </v-container>

              <!-- Buttons -->
              <!-- <v-container fluid class="p0">
                  <v-layout  row class="mt20">
                    <v-flex xs12 sm6>
                        <v-btn small block class="red darken-1 white--text"
                        @click="cancelViewBatch()">
                        CLOSE
                        </v-btn>
                    </v-flex>
                  </v-layout>
              </v-container> -->
            </v-card-text>
          </v-card>
      </v-dialog>

      <view-batch-applications
        :batch="batch"
        :applications="batchApplications">
      </view-batch-applications>

      <!-- Snackbar to show success message  -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="showSuccessSnackbar"
        >
        {{ successMessage }}
      </v-snackbar>

      <!-- Snackbar to show error -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="showErrorSnackbar"
        >
        {{ errorMessage }}
      </v-snackbar>
      

    </div>
</template>


<script>
import {
  SubmitBatches,
  DeleteBatches
} from "../../../../../services/BatchServices";
import { SaveBatchesOwnedByStockbrokersToStore } from "../../../../../services/MutationServices";
import ViewBatchApplications from "./ViewBatchApplications";
import { mapGetters } from "vuex";

export default {
  components: {
    ViewBatchApplications
  },
  props: {
    batch: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      errorMessage: "Error occured",
      successMessage: "Successfully completed",
      snackbarTimeout: 4000,
      showSuccessSnackbar: false,
      showErrorSnackbar: false,
      showViewBatchDialog: false,
      isSubmittingBatch: false,
      isDeletingBatch: false,
      batchSubmitted: false,
      batchDeleted: false,
      batchSubmissionFailed: false,
      batchDeletingFailed: false
    };
  },

  computed: {
    ...mapGetters({
      allApplications: "allApplications",
      companyID: "getCompanyID"
    }),

    // get the applications that belong to this batch
    batchApplications() {
      const batchContents = this.batch.contents ? this.batch.contents : [];
      const applicationIds = batchContents.map(
        application => application.application_id
      );

      const batchApplications = this.allApplications.filter(application => {
        return applicationIds.indexOf(application.id) !== -1;
      });

      return batchApplications;
    }
  },

  methods: {
    cancelViewBatch: function() {
      // Close the dialog modal
      this.showViewBatchDialog = false;
    },

    refreshBatches: function() {
      SaveBatchesOwnedByStockbrokersToStore(this.companyID);
    },

    /**
     * Submit a batch
     */
    submitBatch: function(batch) {
      this.isSubmittingBatch = true;

      const data = {
        batches: [
          {
            batch_id: batch.id
          }
        ]
      };

      SubmitBatches(data)
        .then(response => {
          this.isSubmittingBatch = false;
          this.successMessage = response.data.message;
          this.showSuccessSnackbar = true;
          this.refreshBatches();
          this.cancelViewBatch();
        })
        .catch(err => {
          console.log(err);
          this.isSubmittingBatch = false;
          this.showErrorSnackbar = true;
        });
    },

    deleteBatch: function(batch) {
      this.isDeletingBatch = true;
      const data = {
        batch_id: batch.id
      };

      DeleteBatches(data)
        .then(response => {
          this.isDeletingBatch = false;
          this.successMessage = response.data.message;
          this.showSuccessSnackbar = true;
          this.refreshBatches();
          this.cancelViewBatch();
        })
        .catch(err => {
          this.isDeletingBatch = false;
          this.showErrorSnackbar = true;
          console.log(err);
        });
    },

    /**
     * Delete a batch
     */
    // deleteBatch: function(batch) {
    //   console.log(batch);
    // },

    viewBatchApplications(event) {
      console.log("clicked");
      document.querySelector("#openViewBatchApplicationsDialog").click();

      event.stopPropagation();
      this.cancelViewBatch();
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



