import * as ApiUrls from "./ApiUrlService";

import axios from "axios";

/**
 * Get all applications submitted to the authenticated stockbroker
 *
 * @param stockbroker_id
 */
export const GetAllApplications = stockbroker_id => {
  return axios({
    url: ApiUrls.ApplicationsResourceUrl + stockbroker_id,
    method: "get"
  });
};

/**
 * Reject a pending application
 *
 * @param application_id
 */
export const RejectApplication = data => {
  return axios({
    url: ApiUrls.RejectApplicationUrl,
    method: "post",
    data
  });
};

/**
 * Accept a pending application
 *
 * @param application_id
 */
export const AcceptApplication = data => {
  return axios({
    url: ApiUrls.AcceptApplicationUrl,
    method: "post",
    data
  });
};

/**
 * Edit a pending application
 *
 * @param application_id
 */
export const EditApplication = data => {
  return axios({
    url: ApiUrls.EditApplicationUrl,
    method: "post",
    data
  });
};

/**
 * Submit a pending application
 *
 * @param application_id
 */
export const SubmitApplication = data => {
  return axios({
    url: ApiUrls.SubmitApplicationUrl,
    method: "post",
    data
  });
};



