import * as ApiUrls from "./ApiUrlService";

import axios from "axios";

/**
 * Services which handles HTTP calls regarding
 * user creation and management for the agent module of the application
 */

/**
 * Authenticate an agent, and get their details in return.
 * The information returned includes
 *  + agents under the stockbroker
 *  + ipo applications made to the stockbroker
 */
export const AuthenticateAgent = credentials => {
  return axios({
    url: ApiUrls.AuthenticateAgentUrl,
    method: "post",
    data: credentials
  });
};

/**
 * Add a newUser as a sub user under the logged in agent account
 *
 * @param {object} newUser
 */
export const AddNewUser = newUser => {
  return axios({
    url: ApiUrls.StockBrokerResourceUrl,
    method: "post",
    data: newUser
  });
};

/**
 * Get information about a stockbroker.
 * The information returned includes
 *  + agents under the stockbroker
 *  + ipo applications made to the stockbroker
 *
 * @param stockbroker_id
 */
export const GetStockbrokerInfo = stockbroker_id => {
  return axios({
    url: ApiUrls.GetStockbrokerCompanyUrl + stockbroker_id,
    method: "get",
    data: stockbroker_id
  });
};

/**
 * Update a stockbroking user's record
 *
 * @param user
 */
export const UpdateUser = user => {
  return axios({
    url: ApiUrls.EditAgentUrl + user.id,
    method: "put",
    data: user
  });
};

/**
 * Delete a stockbroker
 *
 * @param stockbroker_id
 */
export const DeleteUser = stockbroker_id => {
  return axios({
    url: ApiUrls.StockBrokerResourceUrl + stockbroker_id,
    method: "delete"
  });
};



