<template>
    <div>
      <v-dialog v-model="showApproveApplicationDialog" lazy absolute persistent width="40%">
          <!--Button used to trigger the opening of the edit user dialog -->
          <v-btn id="openApproveApplicationDialog"
          style="display: none" color="primary" dark slot="activator" ref="triggerButton">
          </v-btn>

          <v-card>
            <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px">APPROVE APPLICATION</h5>
            </v-card-title>
              <!-- Content -->
            <v-card-text>
                <v-layout>
                <v-flex>
                    
                    You are about to approve the following application. 

                <table class="table mt20">
                    <tr>
                    <th>Applicant Name</th>
                    <td>{{application.formattedApplicantName}}</td>
                    </tr>
                    <tr>
                    <th>Application Date</th>
                    <td>{{application.application_date | moment('DD-MMM-YYYY')}}</td>
                    </tr>
                    <!-- <tr>
                    <th>No Of Units</th>
                    <td>{{application.units}}</td>
                    </tr> -->
                </table>
                </v-flex>
                </v-layout>

                <!-- Loading Icon when approving application-->
                <v-layout v-if="isAcceptingApplication"  row class=" d-flex justify-center ">
                  <v-flex class="xs6 d-flex justify-center align-center">
                    <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
                    <span class="font-size-12 ml5">Accepting Application</span>
                  </v-flex>
                </v-layout>

                <!-- Buttons -->
                <v-container fluid class="p0 ">
                    <v-layout  row class="mt20">
                        <v-flex xs12 lg6>
                        <v-btn small block info class=" darken-1 white--text"
                        :loading="isAcceptingApplication" :disabled="isAcceptingApplication"
                        @click="approveApplication(application.id)">
                        APPROVE THIS APPLICATION
                        </v-btn>
                    </v-flex>
                    <v-flex xs12 lg6>
                        <v-btn small block class="red darken-1 white--text"
                        @click="cancelApproveApplication()">
                        DO NOT APPROVE THIS APPLICATION
                        </v-btn>
                    </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
          </v-card>
      </v-dialog>

      <!-- Snackbar to show success message when application is approved -->
      <v-snackbar success
        :timeout="snackbarTimeout"
        :top="true"
        :color="'success'"
        v-model="applicationApproved"
        >
        Application has been approved successfully
      </v-snackbar>

      <!-- Snackbar to show error message when application approval fails -->
      <v-snackbar error
        :timeout="snackbarTimeout"
        :top="true"
        :color="'error'"
        v-model="applicationApprovalFailed"
        >
        Failed to approve the application
      </v-snackbar>
    </div>
</template>


<script>
import { AcceptApplication } from "../../../../../services/ApplicationService";
import { SaveStockbrokerInfoToStore } from "../../../../../services/MutationServices";
import { mapGetters } from "vuex";
export default {
  props: {
    application: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      snackbarTimeout: 4000,
      applicationApproved: false,
      applicationApprovalFailed: false,
      isAcceptingApplication: false,
      showApproveApplicationDialog: false,
      valid: false,
      requiredRule: [v => !!v || "Required"]
    };
  },

  computed: {
    ...mapGetters({
      userID: "getAgentID",
      companyID: "getCompanyID"
    })
  },

  methods: {
    /**
     * Show the confirmation dialog box before approving an application
     */
    showApproveApplication(application, event) {
      // Set the selected user so the popup dialog can be populated with the selected user
      this.application = application;

      // Display the edit popup modal/dialog
      this.$refs.triggerButton.click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    },

    /**
     * Used to reject an application
     */
    approveApplication(applicationId) {
      this.$validator.validateAll().then(result => {
        // The form is invalid
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.approveApplicationForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          // Send the rejection request to the server

          const approvalRequest = {
            user_id: this.userID,
            application_id: applicationId,
            token: this.application.token
          };

          this.isAcceptingApplication = true;

          AcceptApplication(approvalRequest)
            .then(response => {
              this.isAcceptingApplication = false;
              this.applicationApproved = true;

              // Reload the applications data owned by the broker
              SaveStockbrokerInfoToStore(this.companyID);

              // Hide the dialog box
              this.cancelApproveApplication();
            })
            .catch(err => {
              console.log(err);
              this.isAcceptingApplication = false;
              this.applicationApprovalFailed = true;

              // Hide the dialog box
              this.cancelApproveApplication();
            });
        }
      });
    },

    cancelApproveApplication: function() {
      // Close the dialog modal
      this.showApproveApplicationDialog = false;

      // Clear the selected user
      //   this.application = {};
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



